.SecondaryNav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0 3.3%;
    box-sizing: border-box;
    position: fixed;
    top: 60px;
    transform: translateY(-100%);
    transition: transform 200ms ease-in;
    background: white;
    z-index: 998;
}

.SecondaryNav[data-show="true"] {
    transform: translateY(0%);
}

.SecondaryNavLinkContainer {
    display: flex;
    flex-direction: column;
}

.SecondaryNavLink {
    white-space: nowrap;
    font-size: 40px;
    text-transform: uppercase;
    color: black;
    text-decoration: none;
}

.SecondaryNavLinkIcon {
    width: 0.6em !important;
    padding-right: 10px;
}

@media only screen and (min-width: 375px) {
    .SecondaryNav {
        top: 65px;
    }

    .SecondaryNavLink {
        font-size: 45px;
    }
}

@media only screen and (min-width: 480px) {
    .SecondaryNav {
        top: 70px;
    }

    .SecondaryNavLink {
        font-size: 50px;
    }
}

@media only screen and (min-width: 540px) {
    .SecondaryNav {
        top: 75px;
    }

    .SecondaryNavLink {
        font-size: 55px;
    }
}

@media only screen and (min-width: 768px) {
    .SecondaryNav {
        padding: 0 5.5%;
        top: 80px;
    }

    .SecondaryNavLink {
        font-size: 60px;
    }
}