.Home {
    padding: 0 3.3%;
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: cover;
}

.Observations {
    background-image: url('../../assets/images/Observations_bg.png');
}

.Run-River {
    background-image: url('../../assets/images/Run_River_bg.jpg');
}

.Learning {
    background-image: url('../../assets/images/Learning_bg.jpg');
}

.HomeLink {
    color: black;
    text-decoration: none;
}

.HomeLinkHeader {
    font-size: 25px;
    font-weight: normal;
    margin: 10px 0 0;
    text-transform: uppercase;
}

.HomeLinkSubheader {
    font-family: "Spectral Light", serif;
    font-size: 20px;
    font-weight: normal;
    margin: 0 0 20px;
}

.HomeLinkHeaderCircle {
    display: none;
}

@media only screen and (min-width: 375px) {
    .HomeLinkHeader {
        font-size: 30px;
    }

    .HomeLinkSubheader {
        font-size: 25px;
    }
}

@media only screen and (min-width: 480px) {
    .HomeLinkHeader {
        font-size: 35px;
    }

    .HomeLinkSubheader {
        font-size: 30px;
    }
}

@media only screen and (min-width: 540px) {
    .HomeLinkHeader {
        font-size: 40px;
    }

    .HomeLinkSubheader {
        font-size: 35px;
    }
}

@media only screen and (min-width: 768px) {
    .Home {
        padding: 0 5.5%;
    }

    .HomeLinkHeader {
        font-size: 45px;
        color: transparent;
        -webkit-text-stroke-color: black;
        -webkit-text-stroke-width: 1px;
    }

    .HomeLinkHeader:hover {
        color: white;
        -webkit-text-stroke-color: unset;
        -webkit-text-stroke-width: 0;
    }

    .HomeLinkSubheader {
        display: none;
    }

    .HomeLinkHeaderCircle {
        color: black;
        font-size: 35px;
        padding-bottom: 2px;
        padding-right: 10px;
        display: inline-block;
    }
}