.PrimaryNav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0 3.3%;
    box-sizing: border-box;
    position: fixed;
    height: 60px;
    top: 0;
    transition: top 200ms ease-in;
    background: white;
    z-index: 999;
}

.PrimaryNavLink {
    font-size: 40px;
    text-transform: uppercase;
    color: black;
    text-decoration: none;
}

.PrimaryNavBtn {
    display: block;
    margin: 0;
    padding: 0;
    font-size: 40px;
    outline: none;
    border: none;
    background: white;
}

.PrimaryNavLinkIconTimes {
    width: 0.5em !important;
}

.PrimaryNavLinkIconEllipsis {
    width: 0.8em !important;
}

@media only screen and (min-width: 375px) {
    .PrimaryNav {
        height: 65px;
    }

    .PrimaryNavLink, .PrimaryNavBtn {
        font-size: 45px;
    }
}

@media only screen and (min-width: 480px) {
    .PrimaryNav {
        height: 70px;
    }

    .PrimaryNavLink, .PrimaryNavBtn {
        font-size: 50px;
    }
}

@media only screen and (min-width: 540px) {
    .PrimaryNav {
        height: 75px;
    }

    .PrimaryNavLink, .PrimaryNavBtn {
        font-size: 55px;
    }
}

@media only screen and (min-width: 768px) {
    .PrimaryNav {
        padding: 0 5.5%;
        height: 80px;
    }

    .PrimaryNavLink, .PrimaryNavBtn {
        font-size: 60px;
    }
}